         <tab-item>
           列表
         </tab-item>
         <tab-item>
           笔记
         </tab-item>
       </tab>-->
     </section>
     <component :is="view"></component>
  </yd-layout>
</template>

<script>
  import List from './StudyList.vue'
  import Note from './NoteList.vue'
  export default{
    components: {
      List,
      Note
    },
    data () {
      return {
        index: 0
      }
    },
    computed: {
      view () {
        return this.index ? 'note' : 'list'
      }
    }

  }
</script>

<style scoped>
  video{
    width: 100%;
  }
</style>



