import Vue from 'vue'
import Router from 'vue-router'
import Index from '../page/index/Index.vue'
import Info from '../page/course/CourseInfo.vue'
import Study from '../page/course/Study.vue'
Vue.use(Router)
var routers = [
  {
    path: '/',
    redirect: '/index'
  },
  {
    name: 'index',
    path: '/index',
    component: Index
  },
  {
    name: 'info',
    path: '/info',
    component: Info
  },
  {
    name: 'study',
    path: '/study',
    component: Study
  }
]

export default new Router({
  routes: routers
})



